.portada {
  display: flex;
  flex-direction: column;

  background: url("../img/bg_home.jpg") no-repeat left top;
  background-size: cover;
  height: 100vh;

  @media screen and (max-width: 575px) {
    height: auto !important;
  }
}

header.header {
  background-color: #C1CE00;
  height: 90px;
  margin: 0;
  padding: 0;

  .header__menu {
    color: $white;
  }

  .header__logo {
    margin-right: -15px;
  }
}

.main {
  flex: 1 1 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;

  flex-direction: column;
}

.main__copy {
  margin: 100px auto 80px;
  @media screen and (max-width: 575px) {
    margin: 36px auto;
  }

  h1 {
    letter-spacing: 5px;

    @media screen and (max-width: 575px) {
      font-size: 20px;
    }
  }

  span {
    font-size: 20px;
    letter-spacing: 5px;
    @media screen and (max-width: 575px) {
      font-size: 16px;
    }
  }
}

.main__ambientes {
  margin-bottom: 48px;
}
.ambiente {

  font-family: 'nexa', sans-serif;
  font-weight: 400;

  margin-bottom: 24px;
  &__imagen {
    img {
      width: 80%;
      max-width: 180px;
      @media screen and (max-width: 575px) {
        max-width: 120px;
      }
    }
    &:hover {
      cursor:pointer;
      opacity: .8;
    }
  }

  &__titulo {
    font-size: 18px;
    letter-spacing: 3px;
    margin: 1rem auto;
    @media screen and (max-width: 575px) {
      font-size: 16px;
      letter-spacing: 1.5px;
    }
  }

  .linea {
    width: 80%;
    border: 1px solid rgba(0,0,0,.8);
    margin: 0 auto 24px;
  }

  &__medidas {
    display: flex;
    justify-content: center;
  }

  &__talla {
    width: 40px;
    height: 40px;
    line-height: 42px;
    margin-right: 12px;
    text-align: center;
    &:last-child {
      margin-right: 0;
    }
    span {
      font-size: 16px;
      color: $white;
    }

    &:hover {
      cursor: pointer;
      background-color: #C1CE00 !important;
    }
  }


  &--AZ {
    .ambiente__talla {
      background-color: $brand-color-azul;
    }
  }

  &--NA {
    .ambiente__talla {
      background-color: $brand-color-naranja;
    }
  }

  &--CAF {
    .ambiente__talla {
      background-color: $brand-color-cafe;
    }
  }

  &--AGM {
    .ambiente__talla {
      background-color: $brand-color-aguamarina;
    }
  }
}